@charset "UTF-8";
.label {
  color: rgba(31, 32, 65, 0.25);
  opacity: 0.25;
  justify-self: end;
  padding-right: 1.8rem;
  font-weight: normal; }

html {
  font-family: 'Open Sans';
  font-size: 14px;
  line-height: 1.7;
  color: #1F2041; }
  @media (max-width: 400px) {
    html {
      font-size: 12px; } }

h1 {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h2, .dropdown__option-iteration {
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h3, .dropdown__item, .dropdown__option-quantity, .dropdown__button {
  font-weight: bold;
  font-size: 0.9rem;
  line-height: 1.8;
  letter-spacing: 0.001em;
  /*0.73*/
  font-family: Montserrat;
  text-transform: uppercase; }

.dropdown {
  box-sizing: border-box;
  height: 3.15rem;
  cursor: text;
  width: 100%;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
  outline: none;
  position: relative;
  color: rgba(31, 32, 65, 0.25);
  padding: 1rem; }

.dropdown__i, .dropdown__placeholder:after {
  direction: ltr;
  display: inline-block;
  font-family: 'Material Icons';
  color: rgba(31, 32, 65, 0.75);
  font-size: 24px;
  font-style: normal;
  font-weight: normal;
  letter-spacing: normal;
  line-height: 1;
  text-transform: none;
  white-space: nowrap;
  word-wrap: normal;
  font-feature-settings: 'liga';
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
  content: "expand_more";
  position: absolute;
  top: 0.7rem;
  right: 0.6rem;
  cursor: pointer;
  pointer-events: all; }

.dropdown:hover, .dropdown_state_hovered {
  border-width: 2px;
  border-color: rgba(31, 32, 65, 0.5); }

.dropdown:hover .dropdown__placeholder, .dropdown_state_hovered .dropdown__placeholder {
  color: rgba(31, 32, 65, 0.5); }

.dropdown__placeholder {
  color: rgba(31, 32, 65, 0.25); }

/*
_state_ - в js добавить! (expanded и hovered(?))
*/
.dropdown__placeholder {
  overflow: hidden;
  cursor: pointer; }

.dropdown_state_expanded {
  border-width: 2px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0; }
  .dropdown_state_expanded .dropdown__placeholder {
    pointer-events: none; }
    .dropdown_state_expanded .dropdown__placeholder:after {
      pointer-events: all; }
  .dropdown_state_expanded .dropdown__customization {
    position: absolute;
    max-height: 800px;
    top: 3rem;
    padding-top: 0.3rem;
    left: -2px;
    right: -2px;
    z-index: 1000;
    border: 2px solid rgba(31, 32, 65, 0.25);
    border-top: none;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    box-sizing: border-box; }

.dropdown__customization {
  max-height: 0;
  background-color: white;
  overflow: hidden;
  transition: max-height 100ms ease-in;
  -webkit-transition: max-height 100ms ease-in; }

.dropdown__options {
  padding-bottom: 0.25rem; }

.dropdown__item {
  height: 2.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.04rem 0.4rem 0.04rem 1rem; }

.dropdown__option {
  justify-self: flex-start;
  color: #1F2041; }

.dropdown__option-setter {
  display: flex;
  align-items: center; }

.dropdown__option-iteration {
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 50%;
  padding: 5px;
  width: 2.1rem;
  height: 2.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(31, 32, 65, 0.25);
  font-weight: normal;
  cursor: pointer;
  user-select: none; }
  .dropdown__option-iteration_disabled {
    border-color: rgba(31, 32, 65, 0.05);
    cursor: default; }

.dropdown__option-quantity {
  color: #1F2041;
  padding: 0.85rem; }

.dropdown__buttons {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  justify-content: space-between; }

.dropdown__button {
  padding: 0.8rem 0.5rem 0.5rem;
  cursor: pointer;
  outline: none; }
  .dropdown__button_target_apply {
    color: #BC9CFF; }
  .dropdown__button_target_clear {
    color: rgba(31, 32, 65, 0.5); }
