@charset "UTF-8";
.label {
  color: rgba(31, 32, 65, 0.25);
  opacity: 0.25;
  justify-self: end;
  padding-right: 1.8rem;
  font-weight: normal; }

html {
  font-family: 'Open Sans';
  font-size: 14px;
  line-height: 1.7;
  color: #1F2041; }
  @media (max-width: 400px) {
    html {
      font-size: 12px; } }

h1 {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h2 {
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h3 {
  font-weight: bold;
  font-size: 0.9rem;
  line-height: 1.8;
  letter-spacing: 0.001em;
  /*0.73*/
  font-family: Montserrat;
  text-transform: uppercase; }

.footer_type_wide .footer__top, .footer_type_wide .footer__bottom {
  padding-left: 10rem !important;
  padding-right: 10rem !important; }
  @media only screen and (max-width: 1250px) {
    .footer_type_wide .footer__top, .footer_type_wide .footer__bottom {
      padding-left: 5rem !important;
      padding-right: 5rem !important; } }
  @media only screen and (max-width: 1100px) {
    .footer_type_wide .footer__top, .footer_type_wide .footer__bottom {
      padding-left: 2rem !important;
      padding-right: 2rem !important; } }

.footer {
  background-color: #fff;
  font-family: Montserrat;
  padding-top: 7.4rem;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05); }
  .footer__text {
    color: rgba(31, 32, 65, 0.75);
    line-height: 1.8;
    font-size: 0.95rem; }
  .footer_type_wide {
    background-position-y: 7.1rem; }
    .footer_type_wide .footer__top {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between; }
    .footer_type_wide .footer__info {
      width: 20%;
      flex-wrap: wrap; }
    .footer_type_wide .footer__text {
      padding-top: 1.4rem;
      padding-bottom: 1rem; }
    .footer_type_wide .footer__subscribe-input {
      padding-top: 1.55rem;
      padding-bottom: 1rem; }
    .footer_type_wide .footer__subscribe-block {
      width: 25%; }
    .footer_type_wide .footer__bottom {
      font-size: 0.9rem;
      margin-top: 5.9rem;
      padding-bottom: 1.3rem;
      padding-top: 2rem;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-start;
      border-top: 1px solid rgba(31, 32, 65, 0.25); }
  .footer__menu-item {
    display: block;
    text-decoration: none;
    color: rgba(31, 32, 65, 0.25);
    padding-top: 1.7rem; }
  .footer__header {
    padding-top: 0.2rem;
    font-size: 0.85rem;
    text-transform: uppercase;
    font-weight: bold;
    color: rgba(31, 32, 65, 0.75); }
  .footer__social-icon {
    font-family: "Font Awesome 5 Brands";
    text-decoration: none;
    background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
    -webkit-background-clip: text;
    /* Обрезаем фон по тексту */
    -webkit-text-fill-color: transparent;
    /* Прозрачный текст */
    font-size: 1.8rem;
    padding: 0 1rem; }
  .footer_type_narrow {
    background-position-x: center;
    background-position-y: 7.2rem;
    padding-top: 7.2rem;
    padding-bottom: 7.6rem;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: auto;
    text-align: center; }
    .footer_type_narrow .footer__top {
      width: 45%; }
    .footer_type_narrow .footer__logo {
      padding-bottom: 1.2rem; }
    .footer_type_narrow .footer__menu {
      display: none; }
    .footer_type_narrow .footer__subscribe-block {
      display: none; }
    .footer_type_narrow .footer__copyright {
      display: none; }
    .footer_type_narrow .footer__social {
      padding-top: 2rem; }

@media (max-width: 950px) {
  .footer_type_wide .footer__info {
    width: 50%;
    padding-right: 1rem; }
  .footer_type_wide .footer__subscribe-block {
    width: 50%; }
  .footer__menu {
    order: 2; } }

@media (max-width: 750px) {
  .footer_type_wide .footer__info {
    width: 100%; }
  .footer_type_wide .footer__subscribe-block {
    width: 100%; }
  .footer__menu {
    order: 2; } }

@media (max-width: 540px) {
  .footer_type_wide .footer__copyright {
    padding-bottom: 1rem; } }
