.irs {
  position: relative;
  display: block;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.irs-line {
  position: relative;
  display: block;
  overflow: hidden;
  outline: none !important; }

.irs-bar {
  position: absolute;
  display: block;
  left: 0;
  width: 0; }

.irs-shadow {
  position: absolute;
  display: none;
  left: 0;
  width: 0; }

.irs-handle {
  position: absolute;
  display: block;
  box-sizing: border-box;
  cursor: pointer;
  z-index: 1; }

.irs-handle.type_last {
  z-index: 2; }

.irs-min,
.irs-max {
  position: absolute;
  display: block;
  cursor: default; }

.irs-min {
  left: 0; }

.irs-max {
  right: 0; }

.irs-from,
.irs-to,
.irs-single {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  cursor: default;
  white-space: nowrap; }

.irs-grid {
  position: absolute;
  display: none;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20px; }

.irs-with-grid .irs-grid {
  display: block; }

.irs-disable-mask {
  position: absolute;
  display: block;
  top: 0;
  left: -1%;
  width: 102%;
  height: 100%;
  cursor: default;
  background: rgba(0, 0, 0, 0);
  z-index: 2; }

.lt-ie9 .irs-disable-mask {
  background: #000;
  filter: alpha(opacity=0);
  cursor: not-allowed; }

.irs-disabled {
  opacity: 0.4; }

.irs-hidden-input {
  position: absolute !important;
  display: block !important;
  top: 0 !important;
  left: 0 !important;
  width: 0 !important;
  height: 0 !important;
  font-size: 0 !important;
  line-height: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  overflow: hidden;
  outline: none !important;
  z-index: -9999 !important;
  background: none !important;
  border-style: solid !important;
  border-color: transparent !important; }

.irs--custom {
  height: 50px; }

.irs--custom.irs-with-grid {
  height: 65px; }

.irs--custom .irs-line {
  top: 18px;
  height: 8px;
  background-color: white;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px; }

.irs--custom .irs-bar {
  top: 19px;
  height: 6px;
  background-color: #006cfa;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%); }

.irs--custom .irs-bar--single {
  border-radius: 4px 0 0 4px; }

.irs--custom .irs-shadow {
  display: none;
  height: 4px;
  bottom: 21px;
  background-color: rgba(222, 228, 236, 0.5); }

.irs--custom .irs-handle {
  top: 14px;
  width: 16px;
  height: 16px;
  border: 2px solid white;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
  border-radius: 50%; }

.irs--custom .irs-handle.state_hover,
.irs--custom .irs-handle:hover {
  background-color: #f0f6ff; }

.irs--custom .irs-min,
.irs--custom .irs-max {
  color: #333;
  font-size: 14px;
  line-height: 1;
  top: 0;
  padding: 3px 5px;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 4px; }

.irs--custom .irs-from,
.irs--custom .irs-to {
  opacity: 0; }

.irs--custom .irs-from,
.irs--custom .irs-to,
.irs--custom .irs-single {
  font-size: 14px;
  line-height: 1;
  text-shadow: none;
  padding: 3px 5px;
  background-color: #006cfa;
  color: white;
  border-radius: 4px; }

.irs--custom .irs-from:before,
.irs--custom .irs-to:before,
.irs--custom .irs-single:before {
  position: absolute;
  display: block;
  content: "";
  bottom: -6px;
  left: 50%;
  width: 0;
  height: 0;
  margin-left: -3px;
  overflow: hidden;
  border: 3px solid transparent;
  border-top-color: #006cfa; }

.irs--custom .irs-grid {
  height: 25px; }

.irs--custom .irs-grid-pol {
  background-color: #dedede; }

.irs--custom .irs-grid-text {
  color: silver;
  font-size: 13px; }
