    margin-bottom: 1.5rem;
    padding-top: 0.4rem;
    align-items: flex-end; }
    .card__room-data__category {
      color: #BC9CFF;
      font-size: 1.2rem;
      padding-left: 0.4rem; }
    .card__room-data__period {
      font-weight: normal;
      font-size: 0.85rem;
      color: rgba(31, 32, 65, 0.5); }
      .card__room-data__period:before {
        content: "\20BD"; }
    .card__room-data__number {
      font-weight: bold;
      font-size: 1.7rem; }
      .card__room-data__number:before {
        content: "\2116";
        font-size: 1rem;
