.label {
  color: rgba(31, 32, 65, 0.25);
  opacity: 0.25;
  justify-self: end;
  padding-right: 1.8rem;
  font-weight: normal; }

html {
  font-family: 'Open Sans';
  font-size: 14px;
  line-height: 1.7;
  color: #1F2041; }
  @media (max-width: 400px) {
    html {
      font-size: 12px; } }

h1 {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h2 {
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h3 {
  font-weight: bold;
  font-size: 0.9rem;
  line-height: 1.8;
  letter-spacing: 0.001em;
  /*0.73*/
  font-family: Montserrat;
  text-transform: uppercase; }

.feature {
  font-family: Montserrat;
  width: 85%;
  display: grid;
  grid-template: 1.8rem 2.7rem / 3.8rem 1fr;
  padding-top: 1.8rem;
  padding-left: 0.3rem;
  grid-template-areas: "img name" "img descr";
  border-bottom: 1px solid rgba(31, 32, 65, 0.05); }
  .feature__image {
    grid-area: img; }
  .feature__name {
    color: rgba(31, 32, 65, 0.75);
    grid-area: name;
    font-weight: bold; }
  .feature__description {
    grid-area: descr;
    color: rgba(31, 32, 65, 0.5); }
