@import '~Common/basics';
.rate {
    padding-top: 1rem;
    display: inline-block;
    padding-right: 1.3rem;
}
.rate_ratable {
    cursor: pointer;
}

.rate__star {
    position: relative;
    display: inline-block;
    padding-right: 1px;
    width: 1.7rem;
    height: 1.7rem;
    background: url('~Icons/star_border.svg');
    background-size: cover;
    &_checked {
        background: url('~Icons/star.svg');
        background-size: cover;
    }
}
