.label {
  color: rgba(31, 32, 65, 0.25);
  opacity: 0.25;
  justify-self: end;
  padding-right: 1.8rem;
  font-weight: normal; }

html {
  font-family: 'Open Sans';
  font-size: 14px;
  line-height: 1.7;
  color: #1F2041; }
  @media (max-width: 400px) {
    html {
      font-size: 12px; } }

h1 {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h2 {
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h3 {
  font-weight: bold;
  font-size: 0.9rem;
  line-height: 1.8;
  letter-spacing: 0.001em;
  /*0.73*/
  font-family: Montserrat;
  text-transform: uppercase; }

.feedback {
  display: grid;
  font-family: Montserrat;
  grid-template: 2rem 1.4rem 2rem 1fr / 4rem 1fr;
  grid-template-areas: 'ava  name' 'ava   time' 'likes feed' 'likes feed'; }
  .feedback__avatar {
    grid-area: ava; }
  .feedback__likes {
    grid-area: likes;
    padding-left: 0.15rem; }
  .feedback__time {
    grid-area: time; }
  .feedback__feed {
    grid-area: feed;
    padding-top: 0.8rem;
    line-height: 1.65; }
  .feedback__name {
    grid-area: name;
    padding-top: 0.4rem;
    font-weight: bold; }
