.label {
  color: rgba(31, 32, 65, 0.25);
  opacity: 0.25;
  justify-self: end;
  padding-right: 1.8rem;
  font-weight: normal; }

html {
  font-family: 'Open Sans';
  font-size: 14px;
  line-height: 1.7;
  color: #1F2041; }
  @media (max-width: 400px) {
    html {
      font-size: 12px; } }

h1 {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h2 {
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 1.3;
  letter-spacing: 0.001em; }

h3 {
  font-weight: bold;
  font-size: 0.9rem;
  line-height: 1.8;
  letter-spacing: 0.001em;
  /*0.73*/
  font-family: Montserrat;
  text-transform: uppercase; }

.rate {
  padding-top: 1rem;
  display: inline-block;
  padding-right: 1.3rem; }

.rate_ratable {
  cursor: pointer; }

.rate__star {
  position: relative;
  display: inline-block;
  padding-right: 1px;
  width: 1.7rem;
  height: 1.7rem;
  background: url("~Icons/star_border.svg");
  background-size: cover; }
  .rate__star_checked {
    background: url("~Icons/star.svg");
    background-size: cover; }

